.information {
  margin-top: 20px;
  padding: 0 20px;
  display: grid;
  gap: 20px;
}

.programm {
  padding: 18px;
  background-color: $color-bg-block;
  display: flex;
  flex-wrap: wrap;
}

.programm__title {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  padding-left: 19px;
  padding-top: 10px;
}

.programm__desc {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Arial";
  font-style: normal;
  padding-top: 30px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;

  &::after {
    content: "";
    display: inline-block;
    width: 100%;
    height: 1px;
    background-color:#d9d9d9;
    position: absolute;
    bottom: -10%;
    left: 0;
  }
}

.programm__link {
  position: relative;
  display: inline-block;
  text-decoration: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: $color-txt-black;
  margin: 0;
  padding: 0;
  padding-top: 42px;

  &::after {
    content: "";
    display: inline-block;
    background-image: url(../img/icons/arrow.svg);
    background-repeat: no-repeat;
    width: 100%;
    height: 12px;
    position: absolute;
    top: 75%;
    margin-left: 17px;
  }
}
